.small-text {
    font-size: 15px;
    font-family: "Berlin Sans FB";
}

.black-text {
    color: black;
}

.container-wrapper {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    justify-content: unset;
    padding: 15px;
}

.skills-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: rgba(255, 255, 255, 0.3);
    border-radius: 15px;
    width: 40vw;
}

.no-background {
    background-color: rgba(255, 255, 255, 0);
}

.no-border {
    border: none;
}

.make-circle {
    margin-left: 20px;
    margin-right: 20px;
    display: inline block;
}

.orange-circle {
    width: 15px;
    height: 15px;
    border: 0.5px solid orange;
    border-radius: 50%;
    background-color: orange;
}

.white-circle {
    width: 15px;
    height: 15px;
    border: 0.5px solid orange;
    border-radius: 50%;
    background-color: white;
}

.margin-right {
    margin-right: 20px;
}