.margin-top {
    margin-top: 50px;
}

.margin-bottom {
    margin-bottom: 50px;
}

.orange-text {
    color: orange;
}

.blue-text {
    color: rgb(30, 210, 246);
}

.medium-text {
    font-size: 30px;
    font-family: "Arial";
    font-weight: bolder;
}

.berlin {
    font-family: "Berlin Sans FB";
}

.auto-height {
    min-height: 95vh;
    padding-bottom: 5vh;
    height: auto;
}

.noJustify{
    align-items: center;
    justify-content: unset;
}

.noAlign {
    align-items: unset;
    justify-content: center;
}

.align-left {
    text-align: left;
}

.justify {
    align-items: center;
    justify-content: center;
}

.aboutme {
    padding: 30px;
    font-size: 18px;
    font-family: "Berlin Sans FB";
    color: black;
}

.myImage {
    object-fit: contain;
    align-self: flex-start;
    margin-left: 30px;
    width: 150px;
    border-radius: 50%;
}